@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600&display=swap);
body {
  margin: 0;
  font-family:  'Roboto Slab',"Open Sans",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p, li{font-family: "Open Sans",sans-serif;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AppHeader{

}
.LogoHeader{
  width: 400px;
}
.Container{
  width: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Menu ul{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.Menu ul li{
  list-style: none;
}
.Menu ul li a{
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 800;
  color:#0c3c7c;
  padding: 15px 10px;
}
.Menu ul li a:hover{
  color: gray
}
.Ryla{
  background-color: #ddd
}
.Container2{
  width: 60%;
  margin:auto;
  padding-bottom: 45px;
}
.Container2 h1{
  font-size: 2.5em;
  color: #0c3c7c;
  font-weight: 700;
  text-transform: uppercase;
  padding: 45px 0;
  margin: 0;
}
.Container2 p{
  color: #192930;
  font-size: 18px;
  text-align: justify;
  line-height: 25px;
}
.Container2 h2{
  color: #0c3c7c;
  font-weight: 700;
  text-transform: uppercase;
}
.Container2 li{
  color: #192930;
  font-size: 18px;
  text-align: justify;
  line-height: 25px;
}

.Distrito{
  background-color: #fff;
  padding: 45px 0;
}
.Distrito .Container2{
  width: 60%;
  margin:auto;
}
.Distrito .Container2 h1{
  font-size: 2.5em;
  color: #F7A81B;
  font-weight: 700;
  text-transform: uppercase;
  margin: 34px 0;
}
.Distrito .Container2 p{
  color: #192930;
  font-size: 18px;
  text-align: justify;
  line-height: 25px;
}
.Distrito .Container2 h2{
  color: #F7A81B;
  font-weight: 700;
  text-transform: uppercase;
}
.imgConstrucao{
  width: 350px;
}
.Inscricao{
  text-align: center;
  background-color: #0c3c7c;
  padding: 45px 0;
}
.Inscricao .Container2 h1{
  font-size: 2.5em;
  color: #F7A81B;
  font-weight: 700;
  text-transform: uppercase;
  margin: 45px 0 20px;
}
.Inscricao .Container2 p{
  color: white;
  font-size: 18px;
  text-align: center;
  line-height: 25px;
}
.btnAmarelo{
  background-color: #F7A81B;
  padding: 15px 25px;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-weight: 800;
  border-radius: 15px;
}
.Centralizar{
  text-align: center;
}
.Footer{
  background-color: #192930;
  padding: 25px 0;
  color: white;
  text-align: center;
}
.Footer .Amor{
  color:red
}
.Itens{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 35px;
}
.Item{
  width: 32%;
}
.Item h4{
  background: #fff;
  padding: 10px;
  width: 170px;
  border-radius: 15px;
  color:#0c3c7c;
}
.Item li{
  color:white;
  font-size: 14px;
}
.imgRyla{
  width: 49%;
}
.ItensProgramacao{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 35px;
  flex-direction: column;
  width: 49%;
}
.ItemProgramacao{
  width: 100%;
}
.ItemProgramacao h4{
  font-size: 22px;
  margin-bottom: 0px;
  margin-top: 5px
}
.ItemProgramacao li{
  list-style:none;
  padding: 3px 0;
  font-size: 20px
}
.imgdiv{
  width: 48%;

}

.Esquerda{
  text-align: left !important;
}
.DivProg{
  display:flex;
align-items: center;
}
.imgContato{
  float: right;
  width: 40%;
}
.imgProg{
  margin-top:-50px;
}
.palestrantes{display: flex; justify-content: space-between; flex-wrap: wrap;}
.palestrantes .palestrante{width:40% ; text-align: center; background: #eee; margin: 20px; padding: 20px;
  box-shadow: 5px 5px 16px -1px rgba(0,0,0,0.75);}
.palestrantes img{width: 50%; border-radius: 50%;}
@media (max-width: 600px) {
  .Container2, .Distrito .Container2{width: 90%;}
  .Menu{display: none;}
  .imgRyla{width: 100%;}
  .ItensProgramacao, .Itens{flex-direction: column;}
  .ItemProgramacao, .Item{width: 100%;}
  .btnAmarelo{font-size: 14px; padding:10px 5px}
  .DivProg{flex-direction: column;}
  .imgProg{width:100%}
  .ItensProgramacao, .imgdiv{width: 100%;}
  .palestrantes .palestrante{width:97% ;}


}

.Banner{
    background-color: grey;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center;
    background-size: cover;
    text-align:center
}
.Banner h1{
    color:white;
    text-transform: uppercase;
    font-size: 4em;
}
@media (max-width: 600px) {
    .Banner h1{
        font-size: 2em;
    }

}
